import { GspAppModel } from './../models/gsp-app.model';
import { Component, OnInit, Injector, LOCALE_ID,
        Input, OnChanges, SimpleChanges, ViewChild, NgZone } from '@angular/core';
import { HttpService } from '@farris/ide-devkit';
import { TreeNode } from '@farris/ui-treetable';
import { MessagerService } from '@farris/ui-messager';
import { LocaleService } from '@farris/ui-locale';
@Component({
    selector: 'gsp-app-mangaer',
    templateUrl: './gsp-app.component.html'
})
export class GspAppManagerComponent implements OnInit, OnChanges {

    @Input() serverIp = '';
    @Input() path = '';
    /** ææappæ°æ® */
    allAppTreeData = [];
    appIdField = 'id';

    showMask = true;

    appModel: GspAppModel = {
        appInvoks: [
        ],
        code: '',
        creator: '',
        description: '',
        id: this.uuid(),
        layer: '4',
        name: '',
        parentId: '',
        url: ''
    };


    localeSer: LocaleService;

    private get APP_URI() {
        return  this.serverIp + '/api/runtime/sys/v1.0/gspapp/all';
    }

    constructor(
        private http: HttpService,
        private injector: Injector,
        private ngZone: NgZone,
        private messagerSer: MessagerService
    ) {

        this.localeSer = this.injector.get(LocaleService, null);
    }

    ngOnInit() {
        this.initAllAppTree();
        this.appModel.url = this.path;
        this.addInvoke();
    }

    ngOnChanges(changes: SimpleChanges) {

    }

    validate() {
        let msg = '';


        if (!this.appModel.code) {
            msg += this.localeSer.getValue('publishmenu.app.message.appcode'); // '<br> åºç¨ç¼å·ä¸è½ä¸ºç©ºã';
        }

        if (!this.appModel.name) {
            msg += this.localeSer.getValue('publishmenu.app.message.appname'); // '<br> åºç¨åç§°ä¸è½ä¸ºç©ºã';
        }


        if (!this.appModel.url) {
            msg += this.localeSer.getValue('publishmenu.app.message.url'); // '<br> é¨ç½²è·¯å¾ä¸è½ä¸ºç©ºã';
        }


        if (!this.appModel.appInvoks || !this.appModel.appInvoks.length) {
            msg += this.localeSer.getValue('publishmenu.app.message.appInvoks'); // 'è¯·è®¾ç½®åºç¨å¥å£ç¹';
        } else {
            // const inValidInvoks = this.appModel.appInvoks.filter(n => {
            //     return !n.code || !n.name || !n.appEntrance;
            // });

            const _invoksMsg = this.appModel.appInvoks.map((n, idx) => {
                const m = { idx, msg: ''};
                if (!n.name) {
                    m.msg += this.localeSer.getValue('publishmenu.app.message.invoksCode'); // "<br>å¥å£åç§°ä¸è½ä¸ºç©ºã";
                }
                if (!n.code) {
                    m.msg += this.localeSer.getValue('publishmenu.app.message.invoksName'); // "<br>å¥å£ç¼å·ä¸è½ä¸ºç©ºã";
                }
                if (!n.appEntrance) {
                    m.msg +=  this.localeSer.getValue('publishmenu.app.message.invoksAppEntrance'); // "<br>è·¯ç±æ è¯ä¸è½ä¸ºç©ºã";
                }

                return m;
            });

            if (_invoksMsg.length > 0) {
                const appMain = this.localeSer.getValue('publishmenu.app.entry');
                const line =  this.localeSer.getValue('publishmenu.line');
                _invoksMsg.forEach(m => {
                    msg += `<b>${appMain}:</b> <br> ${ line.replace('{line}', m.idx + 1)} , ${m.msg} <br>`;
                });
                // msg += this.localeSer.getValue('publishmenu.app.message.inValidInvoks'); // 'åºç¨å¥å£ç¹ä¿¡æ¯ä¸å®æ´ï¼è¯·æ£æ¥ã';
            }
        }


        if (msg) {
            this.messagerSer.show('warning', msg, { safeHtml: false });
            return false;
        } else {
            return true;
        }
    }

    treeNodeClicked(e) {
        this.appModel.parentId = e.node.data.id;
        this.showMask = false;
    }

    uuid() {
        return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, (c) => {
            const r = Math.random() * 16 | 0, v = c === 'x' ? r : (r & 0x3 | 0x8);
            return v.toString(16);
        });
    }

    addInvoke() {
        this.appModel.appInvoks.push(this.createNewAppInvok());
    }

    removeInvoke(id) {
        this.appModel.appInvoks = this.appModel.appInvoks.filter(n => n.id !== id);
    }

    private initAllAppTree() {
        this.http.get(this.APP_URI).subscribe(data => {
            if (data) {
                this.allAppTreeData = this.convert2TreeNodes(data);
                // console.log(this.allAppTreeData);
            }
        });
    }


    private convert2TreeNodes(data: any[], parentId = ''): TreeNode[] {
        if (data.length) {
            const nodes = data.filter((v) => {
                let pid = v.parentId;
                if (pid === undefined || pid === null) {
                    pid = '';
                }
                return pid === parentId;
            });
            return nodes.map(item => {
                const node = {
                    data: item,
                    children: [],
                    selectable: item.layer === '3'
                };

                node.children = this.convert2TreeNodes(data, item.id);
                return node;
            });
        }
        return [];
    }

    private createNewAppInvok() {
        return {
            id: this.uuid(),
            appId: this.appModel.id,
            code: '',
            appEntrance: '', // è·¯ç±æ è¯
            name: ''
        };
    }
}
