import { Component, OnInit, Input, Injector, NgZone, ViewChild } from '@angular/core';
import { HttpService } from '@farris/ide-devkit';
import { TreeNode, TreeTableComponent } from '@farris/ui-treetable';
import { DatagridComponent } from '@farris/ui-datagrid';
import { cloneDeep } from 'lodash-es';
import { LocaleService } from '@farris/ui-locale';
@Component({
    selector: 'bizobj-operators',
    templateUrl: './bizobject-operators.component.html'
})
export class BizobjectOperatorsComponent implements OnInit {
    @Input() serverIp = '';
    treeData = [];


    columns = [];
    items = [];
    allOperators = [];

    funcModel: any = {};

    selectBizObjectId = '';
    selectBizOpId = '';


    get BIZ_OBJECT_URI() {
        return this.serverIp + '/api/dev/main/v1.0/business-objects';
    }

    get FUNCTION_OPERATORS_URI() {
        return this.serverIp + '/api/runtime/sys/v1.0/funcOperations/all';
    }

    @ViewChild('grid') grid: DatagridComponent;
    @ViewChild('tt') tt: TreeTableComponent;


    get bizObjectId() {
        if (this.tt.selectedRow) {
            return this.tt.selectedRow;
        }
        return '';
    }

    get bizOpId() {
        if (this.grid.selectedRow) {
            return this.grid.selectedRow;
        }

        return '';
    }
    localeSer: LocaleService;
    constructor(
        private http: HttpService,
        private injector: Injector,
        private ngZone: NgZone) {

        this.localeSer = this.injector.get(LocaleService, null);
    }

    ngOnInit(): void {
        this.initTreeData();

        this.columns = [
            { title: this.localeSer.getValue('publishmenu.app.column.code'), field: 'code', width: 200 },
            { title: this.localeSer.getValue('publishmenu.app.column.name'), field: 'name', width: 150 }
        ];

    }

    treeNodeClicked(e) {
        this.grid.clearAll();
        this.items = this.allOperators.filter(n => n.businessId === e.node.data.id);
        if (this.funcModel && Object.keys(this.funcModel).length) {
            const bizOpId = this.funcModel.bizOpId;
            if (!bizOpId) {
                return;
            }
            this.grid.selectRow(bizOpId, false);
        }
    }

    treeNodeUnSelect() {
        this.items = this.allOperators;
    }


    private initTreeData() {
        this.http.get(this.BIZ_OBJECT_URI).subscribe(data => {
            if (data) {
                this.treeData = this.convert2TreeNodes(data);
                setTimeout(() => {
                    if (this.funcModel && Object.keys(this.funcModel).length) {
                        const bizObjectId = this.funcModel.bizObjectId;
                        if (!bizObjectId) {
                            return;
                        }

                        const treeNode = this.tt.findRowNode(bizObjectId);
                        if (treeNode) {
                            treeNode.parents.forEach((id) => {
                                this.tt.expandNode(id);
                            });
                            // this.tt.selectNode(bizObjectId);
                        }

                        this.selectBizObjectId = bizObjectId;
                    }
                }, 200);
            }
        });

        this.http.get(this.FUNCTION_OPERATORS_URI).subscribe( data => {
            if (data) {
                this.items = data.filter(item => item.type === 0);
                this.allOperators = cloneDeep(this.items);
            }
        });
    }


    private convert2TreeNodes(data: any[], parentId = null): TreeNode[] {
        if (data.length) {
            const nodes = data.filter((v) => v.parentID === parentId);
            return nodes.map(item => {
                const node = {
                    data: item,
                    children: [],
                    expanded: parentId === null,
                    selectable: Number(item.isDetail) === 1
                };

                node.children = this.convert2TreeNodes(data, item.id);
                return node;
            });
        }
        return [];
    }
}
